// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS
$color-black: #282A33;
$color-white: #fff;
$color-gray: #828282;;
$color-accent: #7B49F2;
$color-link: #2F80ED;

// FONTS
// stylelint-disable-next-line
$font-family-SFProDispla: "SFProDisplay", sans-serif;

// EASING TRANSITIONS
$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;

// Settings
$desktop: 1025;
$desktop-xl: 1800;
$laptop: 1440;
$tablet: 768;
$mobile: 320;
$desktop-vh: 750;
